.infoGroup{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 500px;
    height: 300px;
    background-color: #fff;
}

.infoGroupComponentDiv {
    display: flex;
    width: 100%;
    justify-content:space-around;
}
.info:first-child span:first-child{
    margin-right: 13%;
    margin-left: 13%;
}
.info {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    background-color: #252525;
    color: #fff;
    height: 50px;
}





